/* jshint ignore:start */

define('ember-people/config/environment', ['ember'], function(Ember) {
  var prefix = 'ember-people';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("ember-people/tests/test-helper");
} else {
  require("ember-people/app")["default"].create({"name":"ember-people","version":"0.0.0.8286cf53"});
}

/* jshint ignore:end */
